/* sanitize.css v. 13.0.0 - https://github.com/csstools/sanitize.css*/
.screen-reader-text {
  height: 1px;
  left: -999px;
  position: absolute;
  top: auto;
  width: 1px;
}
.screen-reader-text:focus {
  background-color: var(--ghost-accent-color);
  color: #FFF;
  display: inline-block;
  font-weight: 700;
  height: auto;
  left: 8px;
  margin: auto;
  padding: 16px 32px;
  position: fixed;
  text-decoration: none;
  top: 8px;
  width: auto;
  z-index: 9999999;
}

.header {
  background-color: var(--ghost-accent-color);
  left: 0;
  padding: 16px 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
}

.header.mobile-open {
  height: 100vh;
}
.header.mobile-open .header__wrapper {
  flex-wrap: wrap;
}
.header.mobile-open .header__toggle-nav {
  order: 1;
}
.header.mobile-open .header__toggle-nav-bar {
  top: 50%;
  transform: rotate(-45deg);
  width: 32px;
}
.header.mobile-open .header__toggle-nav-bar:nth-of-type(2) {
  top: 50%;
  transform: rotate(45deg);
  width: 32px;
}
.header.mobile-open .header__nav {
  display: flex;
  flex: 0 0 100%;
  justify-content: center;
  max-width: 100%;
  padding: 80px 0 60px;
  width: 100%;
}
.header.mobile-open .header__nav .nav {
  align-items: center;
  flex-direction: column;
}
.header.mobile-open .header__nav .nav li {
  font-size: 32px;
  margin-bottom: 40px;
  margin-left: 0px;
}
.header.mobile-open .header__actions {
  display: flex;
  flex: 0 0 100%;
  justify-content: center;
  margin: 0;
  max-width: 100%;
  width: 100%;
}
.header.mobile-open .header__action {
  margin: 0 16px;
}

.header__wrapper {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: 1280px;
  padding: 0 16px;
  align-items: center;
  display: flex;
  position: relative;
}
@media (min-width: 600px) {
  .header__wrapper {
    padding: 0 32px;
  }
}

.header__logo {
  flex: 1 0 auto;
  order: 1;
  padding: 4px 0;
}
@media (min-width: 960px) {
  .header__logo {
    flex: 0 0 auto;
    max-width: auto;
    width: auto;
  }
}
.header__logo img {
  height: 24px;
  width: auto;
}
.header__logo a {
  color: #FFF;
  display: flex;
  font-family: "Roboto Condensed";
  font-size: 24px;
  font-weight: 700;
  line-height: 1;
  text-decoration: none;
}

.header__toggle-nav {
  cursor: pointer;
  flex: 0 0 32px;
  height: 32px;
  max-width: 32px;
  order: 3;
  position: relative;
  width: 32px;
}
@media (min-width: 960px) {
  .header__toggle-nav {
    display: none;
  }
}

.header__toggle-nav-bar {
  background-color: #FFF;
  border-radius: 2px;
  height: 3px;
  right: 0;
  position: absolute;
  top: calc(50% - 7px);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out, width 0.3s ease-in-out;
  width: 32px;
}
.header__toggle-nav-bar:nth-of-type(2) {
  top: calc(50% + 4px);
  width: 24px;
}

.header__nav {
  display: none;
  order: 2;
}
@media (min-width: 960px) {
  .header__nav {
    display: flex;
    flex: 1 0 auto;
    justify-content: flex-end;
  }
}
.header__nav .nav {
  display: flex;
  margin: 0;
}
.header__nav .nav li {
  margin-left: 40px;
  position: relative;
}
.header__nav .nav li a {
  color: #FFF;
  font-family: "Roboto Condensed";
  font-weight: 700;
  padding: 6px 0 7px;
  text-decoration: none;
}
.header__nav .nav li:after {
  background-color: #FFF;
  bottom: -4px;
  content: "";
  height: 2px;
  left: 50%;
  position: absolute;
  transition: left 0.3s ease-in-out, width 0.3s ease-in-out;
  width: 0;
}
.header__nav .nav li:hover:after, .header__nav .nav li.nav-current:after {
  left: 0;
  width: 100%;
}

.header__actions {
  align-items: center;
  display: flex;
  margin-right: 40px;
  order: 2;
}
@media (min-width: 960px) {
  .header__actions {
    margin-left: 32px;
    margin-right: 0;
    order: 3;
  }
}

.header__action {
  margin-left: 24px;
  padding-bottom: 4px;
}
.header__action.gh-portal-close {
  background-color: #FAFAFA;
  border-radius: 16px;
  color: var(--ghost-accent-color);
