  align-self: start;
  display: flex;
  grid-column: 2/3;
  justify-self: end;
  padding-left: 16px;
}

@media (max-width: 400px) {
  .kg-product-card-title-container {
    grid-column: 1/3;
  }

  .kg-product-card-rating {
    grid-column: 1/3;
    justify-self: start;
    margin-top: -15px;
    padding-left: 0;
  }
