.kg-toggle-card {
  background: transparent;
  border-radius: 4px;
  box-shadow: inset 0 0 0 1px rgba(102, 102, 102, 0.2);
  margin: 24px 16px;
  max-width: 768px;
  padding: 0;
}
@media (min-width: 600px) {
  .kg-toggle-card {
