}
.kg-toggle-card[data-kg-toggle-state=close] .kg-toggle-content {
  height: 0;
  opacity: 0;
  overflow: hidden;
  padding: 0;
  position: relative;
  top: -0.5em;
  transition: opacity 0.5s ease, top 0.35s ease;
}
.kg-toggle-card + .kg-toggle-card {
