}

.kg-toggle-content {
  height: auto;
  opacity: 1;
  padding: 0 1.25rem 1.25rem;
  position: relative;
  top: 0;
  transition: opacity 1s ease, top 0.35s ease;
}
@media (min-width: 600px) {
  .kg-toggle-content {
    padding: 0 2rem 2rem;
  }
}

.kg-toggle-card[data-kg-toggle-state=close] svg {
